import { useEffect, useReducer } from "react";
import Layout from "../../components/Layout";
import IconButton from "../../components/IconButton";
import BgLoader from "../../components/BgLoader";
import Table from "../../components/Table/Table";
import Head from "../../components/Table/Head";
import Row from "../../components/Table/Row";
import Cell from "../../components/Table/Cell";
import Body from "../../components/Table/Body"
import {IoMdAdd} from "@react-icons/all-files/io/IoMdAdd"
import Form from "./Form";
import { useDispatch, useSelector } from "react-redux";
import { get, post } from "../../application/middlewares";
import toast from "react-hot-toast";
import NoContent from "../../components/NoContent"
import Switcher from "../../components/Switcher";
import MyPagination from "../../components/Pagination";

function reducer (state, action){
    switch(action.type){
        case "SET_DATA":
            return {
                ...state,
                data:action.payload.discounts,
                count:action.payload.count
            }
        case "CLOSE_MODAL":
            return {
                ...state, 
                visible:false
            }
        case "ADD_FORM":
            return {
                ...state,
                visible:true
            }
        case 'SET_LIMIT':
            return{
                ...state,
                limit:action.payload,
                page:0,
                trigger:!state.trigger,
                loading:true
            }
        case 'SET_PAGE':
            return{
                ...state,
                page:action.payload,
                trigger:!state.trigger,
                loading:true
            }
        case "DEACTIVATE":
            return {
                ...state,
                data:state.data.map(item =>{
                    if(item.id === action.payload){
                        item.is_active = false
                    }return item
                })
            }
        case "ADD_SALE":
            return {
                ...state,
                data:[action.payload, ...state.data]
            }
        default: return state
    }
}

function Sales (){

    const [state, setState] = useReducer(reducer, {
        loading:false, visible:false, data:[], count:0, page:0, limit:20
    })

    const token = useSelector(state=>state.auth.token);
    const dispatch = useDispatch();
    useEffect(()=>{
        dispatch(get({
            url:`api/admin/get-sales`,
            token,
            action: (response) =>{
                // console.log(response)
                if(response.success){
                    setState({type:"SET_DATA", payload:response.data.rows})
                }else{
                    toast.error("Неизвестная ошибка")
                }
            }
        }))
    }, [])// eslint-disable-line react-hooks/exhaustive-deps
    const handleStatus = ({setLoading, value, item_id }) => {
        console.log(value);
        
        dispatch(post({
            url:`api/admin/deactivate-sale/${item_id}`,
            token,
            action: (response) =>{
                if(response.success){
                    setState({type:"DEACTIVATE", payload:item_id})
                }else{
                    toast.error("something went wrong")
                }
            }
        }))
        setLoading(false)
    }

    return (
        <Layout 
            header = {<Header handleClick = {()=>setState({type:"ADD_FORM", payload:""})}/>}
            footer={<MyPagination setPage={(value) => setState({type:'SET_PAGE', payload:value})} limit={+state.limit} count={+state.count} page={state.page} setLimit={(value) => setState({type:'SET_LIMIT', payload:value})}/>}
        >
            <Form
                visible = {state.visible}
                setCloseModal = {()=>setState({type:"CLOSE_MODAL", payload:""})}
                addSale = {(value)=>setState({type:"ADD_SALE", payload:value})}
            />
            <BgLoader loading = {state.loading}/>
            <div className="w-full h-full px-6 overflow-y-auto pb-20 ">
                <Table>
                    <Head>
                        <Row>
                            <>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Номер скидки
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Тип скидки
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Наименование продукта
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Купон
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Минимальное количество
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Скидка
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Работает от .. до
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Деактивировать
                                </div>
                            </Cell>
                            </>
                        </Row>
                    </Head>
                    <Body>
                        { state.data?.length > 0 ?
                            state.data?.map(item => 
                                <Row key={item.id}>
                                    <>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.id}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.discount_name}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.name ? `${item.name}` : "нет товара"}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.coupon ? `${item.coupon}` : "нет купона"}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.min_value}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2">
                                            {item?.discount_value}
                                        </div>
                                    </Cell>
                                    <Cell>
                                        <div className = "p-2 flex flex-col">
                                            <p>{item.low.substring(0, 10)}</p>
                                            <p>{item.upper.substring(0, 10)}</p>
                                        </div>
                                    </Cell>
                                    <Cell>
                                        {(item.is_active && item.active) ? 
                                            <Switcher status = {item.is_active} item_id = {item.id } handleStatus = {handleStatus}/>
                                        :
                                            <Switcher status = {false} item_id = {item.id } handleStatus = {handleStatus}/>
                                        }
                                    </Cell>
                                    </>
                                </Row>
                                )
                                
                            :
                            <>
                                <tr>
                                    <td colSpan="10" >
                                        <div className="flex w-full h-full py-20 justify-center items-center">
                                            <NoContent title="Нет продуктов"/>
                                        </div>
                                    </td>
                                </tr>
                            </>

                        }
                    </Body>
                </Table>
            </div>
        </Layout>
    )
}

const Header = ({handleClick}) =>(
    <div className="flex flex-row justify-between items-center">
        <IconButton tooltip = "Добавить продукт" handleClick={handleClick} icon={<IoMdAdd className="text-2xl "/>}/>
        {/* <div className="w-80">
            <SearchInput action={(value) => handleSearch(value)} placeholder="Поиск"/>
        </div> */}
    </div>
)

export default Sales;